:root {
    --font-pacifico: "Pacifico", cursive;
    --font-systemui: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
}

body {
    text-align: center;
    padding: 20px;
    font-family: var(--font-systemui);
    color: #333;
}

@media (min-width: 768px) {
    body {
        padding-top: 150px;
    }
}

.my-5 {
    margin-top:auto !important;
    margin-bottom:auto !important;
    padding-bottom: 5rem;
}

h1 {
    font-family: var(--font-systemui);
    font-size: 4rem;
}

article {
    display: block;
    text-align: left;
    max-width: 650px;
    margin: 0 auto;
}

a {
    color: #5CAB93;
    text-decoration: none;
}

a:hover {
    color: #333;
    text-decoration: none;
}

.auth-container .card0 .flex-column-reverse {
    min-height: 100vh;
}

.auth-container .card {
    border-radius: 0;
    border: none;
    background-color: #FFF;
}

.auth-container .card1 {
    width: 50%;
    padding: 40px 30px 10px 30px;
}

.auth-container .card2 {
    width: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.auth-container .heading {
    margin-bottom: 60px !important
}

.auth-container ::placeholder {
    color: #000 !important;
    opacity: 1
}

.auth-container .bottom {
    width: 100%;
}

.auth-container .sm-text {
    font-size: 15px
}

@media screen and (max-width: 992px) {
    .auth-container .card1 {
        width: 100%;
        padding: 40px 30px 10px 30px
    }
    .auth-container .card2 {
        width: 100%;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    .auth-container .right {
        margin-top: 100px !important;
        margin-bottom: 100px !important
    }
}

@media screen and (max-width: 768px) {
    .auth-container .container {
        padding: 10px !important
    }
    .auth-container .card2 {
        padding: 50px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    .auth-container .right {
        margin-top: 50px !important;
        margin-bottom: 50px !important
    }
}

.auth-container .cursor {
    cursor: pointer;
}